* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Jura", sans-serif;
}
:root {
  --textadap: calc(1vw + 1vh);
}
@import url("https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&display=swap");

.footer__cyber {
  background-image: url(../../image/main-bg.feea86ca.png);
  background-size: cover;
  background-position: center;
  width: 100%;
  height: fit-content;
  background-color: rgb(42, 107, 206);
  text-align: center;
  padding: 5%;
}
.title {
  background: rgb(57, 197, 230);
  background: linear-gradient(
    90deg,
    rgba(57, 197, 230, 1) 23%,
    rgba(48, 227, 216, 1) 61%,
    rgba(56, 201, 205, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: calc(var(--textadap) * 2.5);
  padding-bottom: 15px;
  border-bottom: 1px solid #fff;
}
.flexbox {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-top: 5%;
  row-gap: 15px;
}
@media (max-width: 600px) {
  .flexbox {
    flex-direction: column;
    align-items: flex-start;
  }
}

.cols__footer a{
  text-decoration: none;
  color: #fff;
  display: block;
  line-height: 150%;
  text-align: left;
  font-size: calc(var(--textadap) * 1);
}

