.white__zone {
  width: 100%;
  background-color: #00000069;
  padding: 3%;
  border-radius: 30px;
  margin-top: 10%;
}
.ours {
  text-align: center;
  font-size: calc(var(--textadap) * 2.4);
}
.wrap__white {
  margin-top: 5%;
  margin: 5% auto;
  max-width: 1400px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.cols__wprap {
  width: 350px;
  border-radius: 30px;
  padding: 25px;
  border: solid 1px;
}
.cols__wprap h2 {
  font-size: calc(var(--textadap) * 1.4);
  margin-bottom: 5%;
  background: rgb(57, 197, 230);
  background: linear-gradient(
    90deg,
    rgba(57, 197, 230, 1) 23%,
    rgba(48, 227, 216, 1) 61%,
    rgba(56, 201, 205, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.cols__wprap li {
  font-size: calc(var(--textadap) * 1);
  border-bottom: #fff solid 0.4px;
  padding: 9px 0px;
  list-style: none;
}
